<template>
  <div>
    <el-row type="flex"
            class="row-bg"
            :gutter="20"
            justify="center">

      <el-col :span="6"
              class="columnflex">
        <el-card class="leftcard">
          <div slot="header"
               class="clearfix">
            <span class="card-header">单位名</span>
          </div>
          <div class="card-body">{{unitName}}</div>
        </el-card>
        <transition name="el-fade-in-linear">
          <el-card v-show="visible"
                   class="leftcard">
            <div slot="header"
                 class="clearfix">
              <span class="card-header">学历</span>
            </div>
            <div class="card-body"> {{degreeName}}</div>

          </el-card>
        </transition>
        <transition name="el-fade-in-linear">
          <el-card v-show="visible"
                   class="leftcard">
            <div slot="header"
                 class="clearfix">
              <span class="card-header">是否在职</span>
            </div>
            <div class="card-body"> {{statusName}}</div>

          </el-card>
        </transition>
      </el-col>

      <el-col :span="6">
        <el-card class="box-card">
          <el-image class="avatar"
                    :src="require('@/assets/wteacher.png')">
          </el-image>
          <p>姓名：{{name}}</p>
          <p>性别：{{sexName}}</p>
          <p>职称：{{zcName}}</p>
          <div>
            <el-button @click="visible = !visible">点击查看详细信息</el-button>
          </div>
        </el-card>
      </el-col>

      <el-col :span="6"
              class="columnflex">
        <el-card class="rightcard">
          <div slot="header"
               class="clearfix">
            <span class="card-header">教师类型</span>
          </div>
          {{typeName}}
        </el-card>

        <transition name="el-fade-in-linear">
          <el-card v-show="visible"
                   class="rightcard">
            <div slot="header"
                 class="clearfix">
              <span class="card-header">邮箱</span>
            </div>
            {{email}}
          </el-card>
        </transition>
        <transition name="el-fade-in-linear">
          <el-card v-show="visible"
                   class="rightcard">
            <div slot="header"
                 class="clearfix">
              <span class="card-header">地址</span>
            </div>
            {{addr}}
          </el-card>
        </transition>
        <transition name="el-fade-in-linear">
          <el-card v-show="visible"
                   class="rightcard">
            <div slot="header"
                 class="clearfix">
              <span class="card-header">电话</span>
            </div>
            {{mobile}}
          </el-card>
        </transition>
      </el-col>
    </el-row>

  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      visible: false,
      alwaysVisible: true,
      name: '',
      sex: '',
      zc: '',
      unitName: '',
      type: '',

      degree: '',
      status: '',
      email: '暂无',
      addr: '暂无',
      mobile: '暂无'
    }
  },
  computed: {
    sexName () {
      if (this.sex == 'w') { return '女' } else { return '男' }
    },
    zcName () {
      switch (this.zc) {
        case 1:
          return '助理讲师'
          break;
        case 2:
          return '讲师'
          break;
        case 3:
          return '教授'
          break;
        case 4:
          return '副教授'
          break;
      }
    },
    typeName () {
      switch (this.zc) {
        case 1:
          return '管理人员'
          break;
        case 2:
          return '责任老师'
          break;
        case 3:
          return '辅导老师'
          break;
      }
    },
    //详细信息
    degreeName () {
      switch (this.degree) {
        case 1:
          return '博士  '
          break;
        case 2:
          return '研究生'
          break;
        case 3:
          return '本科 '
          break;
        case 4:
          return '大专 '
          break;
        case 5:
          return '中专 '
          break;
        case 6:
          return '高中 '
          break;
        case 7:
          return '初中 '
          break;
        case 8:
          return '小学 '
          break;
        case 9:
          return '其他'
          break;
      }
    },
    statusName () {
      switch (this.status) {
        case 0:
          return '不在职'
          break;
        case 1:
          return '在职'
          break;
      }    }
  },
  methods: {
    popover () {
      this.$refs.popover.$el.click()
    },
    getPerInfo (id) {
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/tech/info/perinfo',
        method: 'get',
        params: {
          techId: id,
        }
      }).then(res => {
        this.name = res.data.data.name
        this.sex = res.data.data.sex
        this.zc = res.data.data.zc
        this.unitName = res.data.data.unit
        this.type = res.data.data.type

        this.degree = res.data.data.degree
        this.status = res.data.data.status
        if (res.data.data.email != '') this.email = res.data.data.email
        if (res.data.data.addr != '') this.addr = res.data.data.addr
        if (res.data.data.mobile != '') this.mobile = res.data.data.mobile

        console.log(res);
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    },

  },
  mounted () {
    var id = window.sessionStorage.getItem('storeData')
    this.getPerInfo(id)
  },
  components: {

  }
}
</script>

<style>
.clearfix {
  text-align: center;
}
.card-header {
  color: rgb(69, 69, 69);
}
.avatar {
  border-radius: 20px;
}
.box-card {
  /* width: 300px; */
  text-align: center;
  height: 600px;
}
.hidden {
  visibility: hidden;
}
.columnflex {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.leftcard {
  height: 180px;
  text-align: center;
}
.rightcard {
  text-align: center;
}
.card-body {
  line-height: 75px;
  font-size: 20px;
}
</style>





